/*aqui empieza el header*/
.header {
  background-color: black;
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.header_container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.logo {
  display: flex;
  justify-content: flex-start;
  margin-top: 10px;
  margin-left: 30px;
  margin-bottom: 10px;
  height: 40px;
}

.botton_lines {
  display: flex;
  justify-content: flex-end;
  height: 20px;
  margin-top: 10px;
  margin-left: 30px;
  margin-right: 30px;
  margin-bottom: 10px;
  height: 40px;
}
