:root {
  font-family: Arial, Helvetica, sans-serif;
  text-decoration: none; }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif; }

/*aqui empieza el body*/
body {
  display: block;
  justify-content: center;
  margin-left: 30px;
  margin-right: 30px; }

/*aqui empieza el main*/
main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: fit-content; }

/*aqui empieza el main azul*/
.blue {
  background-color: #509bf5;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center; }

.prueba_premium {
  display: flex;
  justify-content: center; }

.millones {
  display: flex;
  justify-content: center; }

.terminos {
  display: flex;
  justify-content: center;
  font-size: 9px;
  margin-bottom: 30px; }

.green_box {
  display: flex;
  justify-content: center;
  padding: 10px; }

.botton_planes {
  background-color: green;
  color: white; }

/*aqui empieza el main blanco*/
.white {
  background-color: white;
  color: black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  margin-bottom: 30px; }

.white h2 {
  text-align: center; }

.img_descarga {
  margin-top: 30px; }

.white ul li {
  text-align: center;
  list-style: none; }

footer {
  background-color: black;
  font-size: 12px;
  color: white;
  display: flex;
  flex-direction: row;
  height: 100px;
  width: 100%; }

.footer_container {
  display: flex;
  flex-direction: row;
  margin-left: 30px;
  margin-top: 10px;
  justify-content: space-between;
  width: 100%; }

.section_footer_left {
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: flex-start; }

.section_footer_left li {
  list-style: none; }

.section_footer_right {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  margin-right: 30px; }

.españa_flag {
  display: flex;
  flex-direction: row;
  list-style: none;
  margin-bottom: 10px; }

.flag {
  height: 20px;
  margin-left: 5px; }

.section_footer_right p {
  margin-bottom: 10px; }

/*.page__header {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 3rem;
}*/
/*aqui empieza el header*/
.header {
  background-color: black;
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.header_container {
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.logo {
  display: flex;
  justify-content: flex-start;
  margin-top: 10px;
  margin-left: 30px;
  margin-bottom: 10px;
  height: 40px; }

.botton_lines {
  display: flex;
  justify-content: flex-end;
  height: 20px;
  margin-top: 10px;
  margin-left: 30px;
  margin-right: 30px;
  margin-bottom: 10px;
  height: 40px; }
